import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import CreateUser from "../components/CreateUser";
import EditUser from "../components/EditUser";

import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import { withStyles } from "@material-ui/core/styles";

const styles = theme => ({
  root: {},
  container: {
    padding: "80px 0 140px 0",
    background: "white",
    minHeight: "100vh"
  },
  head: {
    color: theme.palette.primary.light
  },
  form: {
    maxWidth: 600,
    margin: "auto"
  },
  group: {
    display: "flex",
    width: "100%",
    flexDirection: "row",
    justifyContent: "center"
  },
  input: {
    width: "100%",
    margin: "0 0 20px 0"
  },
  textarea: {
    width: "100%",
    margin: "0 0 20px 0"
  },
  formButton: {
    width: "100%",
    padding: 20
  },
  formControl: {
    width: "100%"
  },
  radioLabel: {
    width: "100%",
    textAlign: "center"
  }
});

export class UserFormUnconnected extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      form: "createUser"
    };
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    //check for userType status here
  }
  handleChange(e) {
    this.setState({ form: e.target.value });
  }

  render() {
    const { classes } = this.props;
    const { loggedIn } = this.props.appState;
    const redirect = window.localStorage.getItem("redirect");
    return (
      <div className={classes.container} data-test="user-form-container">
        <form>
          <FormControl component="fieldset" className={classes.formControl}>
            <FormLabel component="legend" className={classes.radioLabel}>
              Create or Edit User
            </FormLabel>
            <RadioGroup
              aria-label="User Action"
              name="user_action"
              className={classes.group}
              value={this.state.form}
              onChange={this.handleChange}
            >
              <FormControlLabel
                value="createUser"
                control={<Radio />}
                label="Create User"
              />
              <FormControlLabel
                value="editUser"
                control={<Radio />}
                label="Edit User"
              />
            </RadioGroup>
          </FormControl>
        </form>
        {loggedIn && !redirect && this.state.form === "createUser" && (
          <CreateUser />
        )}
        {loggedIn && !redirect && this.state.form === "editUser" && (
          <EditUser />
        )}
      </div>
    );
  }
}

UserFormUnconnected.propTypes = {
  user: PropTypes.shape({
    form: PropTypes.shape({
      firstName: PropTypes.string,
      lastName: PropTypes.string,
      email: PropTypes.string,
      userType: PromiseRejectionEvent.string
    }),
    loading: PropTypes.bool
  }).isRequired,
  apiUser: PropTypes.shape({
    handleInput: PropTypes.func,
    clearForm: PropTypes.func
  }),
  classes: PropTypes.object
};

const mapStateToProps = state => ({
  appState: state.appState
});

export const UserFormConnected = connect(mapStateToProps)(UserFormUnconnected);

export default withStyles(styles)(UserFormConnected);
